.app-bottom {
  background-color: #cfd8dc;
  position: relative;
  z-index: 3;
  padding: 50px;
  color: #212121;
  font-size: 14px; }
  .app-bottom .content {
    margin: 0 auto;
    max-width: 500px; }
  .app-bottom h2 {
    margin: 0; }
  .app-bottom p {
    text-align: left;
    padding-top: 10px; }
  .app-bottom .settings {
    font-size: 12px;
    padding: 15px;
    background-color: #ffffff;
    text-align: left;
    color: #424242;
    border-radius: 4px;
    margin: 15px 0; }
    .app-bottom .settings p {
      padding: 0; }
    .app-bottom .settings select {
      display: block;
      width: 60%; }
    .app-bottom .settings input[type="radio"] {
      margin-right: 5px; }
    .app-bottom .settings input[type="text"] {
      margin-right: 5px;
      display: block;
      margin-top: 25px;
      width: 60%; }
    .app-bottom .settings label {
      margin-right: 20px;
      display: block; }
    .app-bottom .settings strong {
      padding: 10px 0; }



